// Loading.js
import React from 'react';
import { Background } from './LoadingCSS';
import Spiner from './spiner.gif';
const Loading = () => {
  return (
    <Background>
      <img src={Spiner} alt="" width="6%" />
      {/* <LoadingText></LoadingText> */}
    </Background>
  );
};

export default Loading;
