import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { LoginDiv } from '../../Style/UserCSS';

import firebase from '../../firebase';

import axios from 'axios';

const Register = () => {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [passwordCF, setPasswordCF] = useState('');
  // const [loaded, setLoaded] = useState(false);
  const [nameCheck, setNameCheck] = useState(false);
  const [nameInfo, setNameInfo] = useState('');

  const navigate = useNavigate();

  const RegisterFunc = async (e) => {
    // setLoaded(true);
    e.preventDefault();
    if (!nameCheck) {
      return alert('   .');
    }
    if (password !== passwordCF) {
      return alert('  ');
    }
    if (!(name && email && password && passwordCF)) {
      return alert('   .');
    }

    //   ..  promise async await 
    const createdUser = await firebase
      .auth()
      .createUserWithEmailAndPassword(email, password);

    await createdUser.user.updateProfile({
      displayName: name,
    });
    // console.log(createdUser.user);

    let body = {
      displayName: createdUser.user.multiFactor.user.displayName,
      email: createdUser.user.multiFactor.user.email,
      uid: createdUser.user.multiFactor.user.uid,
    };
    axios.post('/api/user/register', body).then((res) => {
      if (res.data.success) {
        // 
        navigate('/login');
      } else {
        // 
        return alert(' ..');
      }
    });
  };

  const nameCheckHandler = (e) => {
    e.preventDefault();
    if (name === null) {
      return alert(' ');
    }
    let body = {
      displayName: name,
    };
    axios.post('/api/user/namecheck', body).then((res) => {
      if (res.data.success) {
        if (res.data.check) {
          setNameCheck(true);
          setNameInfo(
            <div
              style={{
                fontWeight: 'bold',
                fontFamily: 'Yeon Sung',
              }}
            >
              <span style={{ color: 'green' }}>  </span>
              .
            </div>
          );
        } else {
          setNameInfo(
            <div
              style={{
                fontWeight: 'bold',
                fontFamily: 'Yeon Sung',
              }}
            >
              
              <span
                style={{
                  color: 'red',
                }}
              >
                {' '}
                {' '}
              </span>
              .
            </div>
          );
        }
      }
    });
  };
  return (
    <LoginDiv>
      <form action="">
        <label htmlFor=""></label>
        <input
          type="name"
          value={name}
          disabled={nameCheck} //nameCheck true  input 
          onChange={(e) => {
            setName(e.currentTarget.value);
          }}
        />
        {nameInfo}
        <button onClick={nameCheckHandler}> </button>
        <label htmlFor=""></label>
        <input
          type="email"
          value={email}
          placeholder=" ex) daegon***1@na***.com"
          onChange={(e) => {
            setEmail(e.currentTarget.value);
          }}
        />
        <label htmlFor=""></label>
        <input
          type="password"
          value={password}
          minLength={8}
          placeholder=" 8  ."
          onChange={(e) => {
            setPassword(e.currentTarget.value);
          }}
        />
        <label htmlFor=""></label>
        <input
          type="password"
          value={passwordCF}
          placeholder=" 8  ."
          minLength={8}
          onChange={(e) => {
            setPasswordCF(e.currentTarget.value);
          }}
        />
        {/* disabled={loaded} */}
        <button onClick={RegisterFunc}></button>
      </form>
    </LoginDiv>
  );
};

export default Register;
